// Functions.js PUBLIC

// Fetch de la base de datos
async function getData(p_endpoint) {
	const response = await fetch(p_endpoint);
	const data = await response.json(); 
	return data;
}

// Cargar todos los posts al html
async function loadPosts() {
	var posts = await getData("/get_allposts");

	for(var i = 0; i < posts.length; i++)
	{
		document.getElementById("output").innerHTML += `
			<figure class="gallery__item" onclick="location.href = 'admin';">
				<div class="gallery__item-img"><div class="gallery__item-imginner" style="background-image: url(img/demo3/1.jpg)" data-scroll data-scroll-speed="-0.8"></div></div>
				<figcaption class="gallery__item-caption">
					<h2 class="gallery__item-title" data-scroll data-scroll-speed="1">Title</h2>
					<span class="gallery__item-number" data-scroll data-scroll-speed="1.5">01</span>
				</figcaption>
			</figure>
		`;
	}

	document.getElementById("output").innerHTML += '<div class="gallery__text"><span class="gallery__text-inner" data-scroll data-scroll-speed="1">Maga</span><span data-scroll data-scroll-speed="3" class="gallery__text-inner">sin</span></div>';
}


// load image by Id
async function loadImages(p_id) {

	var images = await getData("/get_im_url/"+p_id);

	console.log(images);

	for(var i = 0; i < images.length; i++)
	{
		document.getElementById("output_images").innerHTML += "<img src='/uploads/" + images[i].url + ".png'><br><br>";
	}
}

